import chai from 'chai';
import chaiHttp from 'chai-http';
import server from '../server';
chai.use(chaiHttp);
chai.should();



/*
  * Test the /POST route
  */
 describe('/Post messages', () => {
  it('it should Create/Post messages', (done) => {
    chai.request(server)
        .post('/api/v1/messages')
        .end((err, res) => {
              res.body.should.be.a([
                {
                    "id": "ae0f724b-8f6d-4234-877a-1848ca412f41",
                    "subject": "Hi",
                    "message": "cn ufjv dk",
                    "parentMessageId": "KALISA",
                    "status": "Unread",
                    "createdOn": 1551871527723
                },
                {
                    "id": "d1081ada-49d5-4a0a-b97e-e10b85ba68eb",
                    "subject": "Test",
                    "message": "I will came",
                    "parentMessageId": "344",
                    "status": "Sent",
                    "createdOn": 1551871605364
                }
            ]);
          done();
        });
  });
});

/*
  * Test the /GET route
  */
 describe('/Get messages', () => {
    it('it should GET all emails/messages', (done) => {
      chai.request(server)
          .get('/api/v1/messages')
          .end((err, res) => {
                res.body.should.be.a('array');
            done();
          });
    });
});